body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
  margin: 0;
  padding: 0;
}

#entire {
  background-color: #000000;
}

#content {
  background-color: rgb(0, 0, 0);
}

#content .topline {
  padding: 30px;
}

#content .topline .logo {
  line-height: 40px;
  color: #fff;
  font-weight: bold;
}

#content .topline .logo img {
  height: 40px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
}

#content .topline .fr a {
  margin-right: 30px;
  text-decoration: none;
  color: #fff;
}

#content .topline .fr .sure {
  color: #a0245f;
}

#content #mainbox {
  width: 480px;
  display: block;
  margin: 60px auto;
}

#littlebox {
  width: 300px;
  display: block;
  margin: 7px auto;
}

#content #mainbox3 {
  width: 480px;
  display: block;
  margin: 130px auto;
}

#content #mainbox h1 {
  color: #fff;
  font-weight: bold;
  text-align: center;
  padding-bottom: 30px;
}

#content #mainbox3 h1 {
  color: #fff;
  font-weight: bold;
  text-align: center;
  padding-bottom: 30px;
}

#content #mainbox2 {
  width: 480px;
  display: block;
  margin: 60px auto;
}

#content #mainbox2 h1 {
  color: #fff;
  font-weight: bold;
  text-align: center;
  padding-bottom: 30px;
}

#content #smallbox {
  width: 480px;
  display: block;
  margin: 30px auto;
}

#content #smallbox h3 {
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-align: center;
  padding-bottom: 5px;
}

#content #smallbox a {
  color: #fff;
}

#content #mainbox input {
  background-color: #959798;
  color: #fff;
  font-size: 24px;
  line-height: 72px;
  padding: 0 30px;
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 20px;
  border: none;
  width: 100%;
}

#littlebox input {
  background-color: #838383;
  color: #fff;
  font-size: 15px;
  line-height: 40px;
  padding: 0 10px;
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 20px;
  border: none;
  width: 100%;
  float: left;
}

#content #mainbox3 input {
  background-color: #959798;
  color: #fff;
  font-size: 24px;
  line-height: 72px;
  padding: 0 30px;
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 20px;
  border: none;
  width: 100%;
}

#content #policy p {
  color: #fff;
  font-weight: bold;
  text-align: center;
}

#content #policy h1 {
  color: #fff;
  font-weight: bold;
  text-align: center;
}

#content #mainbox2 input {
  background-color: #959798;
  color: #fff;
  font-size: 24px;
  line-height: 72px;
  padding: 0 30px;
  box-sizing: border-box;
  border-radius: 12px;
  margin-bottom: 20px;
  border: none;
  width: 100%;
}

#content #mainbox input::placeholder {
  color: #fff;
}

#littlebox input::placeholder {
  color: #fff;
}

#content #mainbox2 input::placeholder {
  color: #fff;
}

#content #mainbox3 input::placeholder {
  color: #fff;
}

#content #mainbox button {
  border: none;
  font-size: 24px;
  line-height: 72px;
  color: #fff;
  text-align: center;
  background-color: #20df7f;
  border-radius: 12px;
  display: block;
  width: 100%;
  cursor: pointer;
}


#content #mainbox3 button {
  border: none;
  font-size: 24px;
  line-height: 72px;
  color: #fff;
  text-align: center;
  background-color: #20df7f;
  border-radius: 12px;
  display: block;
  width: 100%;
  cursor: pointer;
}

#content #mainbox2 button {
  border: none;
  font-size: 24px;
  line-height: 72px;
  color: #fff;
  text-align: center;
  background-color: #20df7f;
  border-radius: 12px;
  display: block;
  width: 100%;
  cursor: pointer;
}

#content #mainbox .btn-group .btn:not(:last-child) {
  margin-right: 30px;
}

#littlebox .btn-group .btn:not(:last-child) {
  margin-right: 5px;
}

#content #mainbox3 .btn-group .btn:not(:last-child) {
  margin-right: 30px;
}

#content #mainbox2 .btn-group .btn:not(:last-child) {
  margin-right: 30px;
}

#content .conlogo {
  width: 600px;
  margin: auto;
  display: block;
  padding: 60px 0;
}

#content .conlogo img {
  height: 120px;
}

#content .nologo h1 {
  height: 120px;
}

#content #special button {
  background-color: #c2bdbd;
}

.btn-group {
  display: flex;
  align-items: center;
}

.btn-group .btn {
  flex-grow: 1;
}

.fl {
  float: left;
}

.fn-clear {
  overflow: hidden;
}

.fr {
  float: right;
}

.fm {
  float: middle;
}

.footer {
  border-top: 1px solid #F4F4F4;
  padding: 60px 30px;
}

.footer .leftbox .flogo {
  height: 30px;
  margin: 30px;
}

.footer .leftbox .line {
  padding: 30px;
}

.footer .leftbox .line .fl {
  height: 30px;
  margin-right: 30px;
}

.footer .leftbox .desc {
  padding: 30px;
  font-size: 14px;
  line-height: 40px;
  color: #ccc;
}

.footer .fr .fl {
  width: 300px;
}

.footer .fr .fl .tit,
.footer .fr .fl .desc a {
  color: #fff;
  display: block;
  font-size: 16px;
  line-height: 30px;
  text-decoration: none;
}

.footer .fr .fl .tit {
  padding-bottom: 30px;
}

.divider{
  height:30px;
}